  color: gray;
  margin: 0;
  text-align: center;
  display: block;
  width: 100%;
}
.certificate-grid .row.no-gutters.align-items-center {
  align-items: center !important;
}
.certificate-grid .card {
  min-height: 140px;
  width: 100%;
  overflow: visible;
  display: flex;
  align-items: center;
  background: transparent;
  border: none;
  padding: 0;
}

.certificate-card-flex {
  width: 100%;
  background: #fff;
  border: none;
  border-radius: 8px;
  padding: 1rem;
  transition: transform 0.3s, box-shadow 0.3s;
}

.certificate-card-flex:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}
.certificate-grid .row {
  align-items: center !important;
}
.certificate-grid .col {
  text-align: center;
  width: 100%;
  min-width: 0;
}
.certificate-grid .card-body {
  width: 100%;
  min-width: 0;
  padding: 0.25rem 0.5rem;
