  text-align: center;
}
.certificate-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 1.5rem;
  padding: 0;
  width: 100%;
}
.experience-page {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.experience-section .row {
  margin-bottom: 2rem;
}

.experience-section .col-lg-6 {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.Heading h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}
.project-heading .p-text {
  margin-bottom: 0.75rem;
}
.Programming {
  display: block;
  width: 100%;
  max-width: 520px;
  height: auto;
  object-fit: contain;
}

.col-lg-6 .Programming {
  margin: 0 auto;
}

@media (min-width: 768px) {
  .Programming {
    max-height: calc(80vh - 120px);
    width: auto;
  }
}
.timeline {
  margin-top: 1.5rem;
}
.timeline-box {
  background: #fff;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 6px;
}
